.breadcrumbs {
    display: flex;
    flex-wrap: wrap;
    padding: 20px 0 10px;
    font-size: 15px;
    a {
        display: flex;
        align-items: flex-start;
    }
    a::after {
        content: "\E005";
        display: inline-block;
        position: static;
        font-family: "icons";
        font-weight: 400;
        font-size: 8px;
        margin: 4px 7px;
    }
    &-section {
        padding-bottom: 15px;
    }
}